/* Footer */

.footer {
    background-color: #fff5f6;
}

.footer-container {
    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: space-between;
    }
}

.footer__title {
    max-width: 234px;
    font-family: Titan One;
    font-size: 18px;
    line-height: 1.17;
    text-transform: uppercase;
    color: #ffa5ba;

    @media screen and (min-width: 768px) {
        max-width: 474px;
        font-size: 26px;
        line-height: 1.14;
    }

    @media screen and (min-width: 1280px) {
        max-width: 472px;
        font-size: 30px;
        line-height: 1.13;
        letter-spacing: 0.04em;
    }
}

.footer__slogan {
    display: block;
    margin-top: 17px;
    margin-bottom: 43px;
    font-family: Titan One;
    font-size: 34px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--accent-color);

    @media screen and (min-width: 768px) {
        font-size: 48px;
        letter-spacing: 0.06em;
    }
    @media screen and (min-width: 1280px) {
        font-size: 58px;
        line-height: 1.14;
    }
}

.footer__socials-list {
    display: flex;
    justify-content: right;
    margin-bottom: 46px;
}

.footer__socials-list li:not(:last-child) {
    margin-right: 28px;
}

.footer__socials-link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    border: none;
    border-radius: 50%;
    background-color: #fea5bb;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        background-color: var(--accent-color);
    }

    @media screen and (min-width: 768px) {
        width: 34px;
        height: 34px;
    }
}

.footer__socials-icon {
    fill: #ffffff;
}

.footer__tel {
    display: flex;
    justify-content: right;
    font-family: DM Sans;
    font-weight: bold;
    font-size: 12px;
    line-height: 1.86;
    color: #000000;
    margin-bottom: 43px;

    @media screen and (min-width: 768px) {
        font-size: 14px;
    }
    @media screen and (min-width: 1280px) {
        font-size: 16px;
        letter-spacing: 0.02em;
    }
}

.footer__tel-link {
    margin-left: 5px;
    color: var(--accent-color);
}

.footer__copyright-container {
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid #e1e1e1;
}

.footer__copyright {
    padding-top: 22px;
    padding-bottom: 25px;
    max-width: 150px;
    font-family: DM Sans;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;

    @media screen and (min-width: 768px) {
        max-width: 100%;
        line-height: 1.86;
        letter-spacing: 0.02em;
        padding-bottom: 18px;
    }
    @media screen and (min-width: 1280px) {
        padding-bottom: 10px;
    }
}
